<template>
  <div class="success">
    <div>{{ $t('checkout.success') }}&nbsp;<i>{{ email }}</i></div>
    <span
      v-if="address"
      class="address"
    >
      <Identicon :address="address" />&nbsp;{{ formatAddress(address) }}
    </span>
  </div>
</template>

<script>
import Identicon from '@/components/Identicon';

export default {
  components: { Identicon },
  props: {
    email: { type: String, required: true },
    address: { type: String, default: '' },
  },
  methods: {
    formatAddress: (address) => `${address.slice(0, 6)}...${address.slice(-4)}`,
  },
};
</script>

