<template>
  <div class="wallet-switcher">
    <Title
      :top="$t('checkout.connect-wallet')"
      :bottom="$t('checkout.select-wallet')"
    />
    <ListItem
      v-for="(name, key) in wallets"
      :key="key"
      :class="{ checked: $store.state.providerName === key }"
      :title="name"
      @click="selectWallet(key)"
    >
      <img
        slot="icon"
        :src="require(`../assets/icons/${key}.svg`)"
      >
      <div slot="right">
        <img
          class="icon"
          :src="require(`../assets/icons/${
            key === $store.state.providerName ? 'checked' : 'unchecked'
          }.svg`)"
        >
      </div>
    </ListItem>
  </div>
</template>

<script>
import Title from '@/components/Title';
import ListItem from '@/components/ListItem';

export default {
  components: { Title, ListItem },
  data() {
    return {
      wallets: this.$t('checkout.wallets'),
    };
  },
  methods: {
    selectWallet(name) {
      this.$store.commit('setProviderName', name);
    },
  },
};
</script>

<style lang="scss" scoped>
@import '../styles/typography';

.wallet-switcher {
  .list-item {
    border-top: 1px solid $color-border;

    &:not(.checked) {
      img {
        filter: grayscale(1);
      }

      ::v-deep .title {
        color: $color-transparent;
      }
    }
  }

  .list-item ~ .list-item {
    border-top: none;
  }
}
</style>
