import Vue from 'vue';
import Vuex from 'vuex';
import { Magic } from 'magic-sdk';
import { ethers } from 'ethers';

Vue.use(Vuex);

const wyreRequest = async (path, options) => {
  const response = await fetch(`${process.env.VUE_APP_WYRE_API_URL}${path}`, {
    ...options,
    headers: {
      Authorization: `Bearer ${process.env.VUE_APP_WYRE_APY_SECRET}`,
      ...options.headers,
    },
  });
  if (response.status !== 200) throw new Error(`Invalid response status: ${response.status}`);
  return response.json();
};

export default new Vuex.Store({
  state: {
    providerName: 'meta-mask',
  },
  mutations: {
    setProviderName(state, name) {
      state.providerName = name;
    },
  },
  actions: {
    async buyTickets({ providerName }, { email }) {
      let provider;
      switch (providerName) {
        case 'magic-link': {
          const magic = new Magic(
            process.env.VUE_APP_MAGIC_LINK_API_KEY,
            process.env.VUE_APP_IS_TEST_ENV ? { network: 'kovan' } : {},
          );
          await magic.auth.loginWithMagicLink({ email });
          provider = new ethers.providers.Web3Provider(magic.rpcProvider);
          break;
        }
        case 'meta-mask':
          if (!window.ethereum) throw new Error('MetaMask is not available');
          await window.ethereum.enable();
          provider = new ethers.providers.Web3Provider(window.ethereum);
          break;
        default:
          throw new Error('Unknown provider');
      }

      const signer = provider.getSigner();
      const address = await signer.getAddress();

      const { reservation } = await wyreRequest('/v3/orders/reserve', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          referrerAccountId: process.env.VUE_APP_WYRE_ACCOUNT_ID,
          amount: process.env.VUE_APP_TICKET_PRICE,
          destCurrency: 'DAI',
          email,
          dest: `ethereum:${address}`,
        }),
      });

      const widget = new window.Wyre({
        env: process.env.VUE_APP_IS_TEST_ENV ? 'test' : 'prod',
        reservation,
        operation: {
          type: 'debitcard-hosted-dialog',
        },
      });
      widget.open();

      await new Promise((resolve) => widget.on('paymentSuccess', resolve));

      const signedMessage = await signer.signMessage('Here will be a payload for GSN call');
      console.log('signedMessage', signedMessage);
    },
  },
});
