<template>
  <div class="checkout">
    <template v-if="step === 1">
      <div class="top">
        <div class="complete">
          <span>{{ $t('checkout.complete') }}</span>
        </div>
        <Product
          :title="$t('checkout.ticket')"
          :token-amount="100"
          :fiat-amount="100"
        />
      </div>
      <div class="bottom">
        <CreateAccount
          v-if="step === 1"
          @submit="proceed"
        />
      </div>
    </template>

    <template v-if="step === 2">
      <div class="top">
        <div class="complete">
          <span>{{ $t('checkout.complete') }}</span>
        </div>
        <Success email="denis_davidyuk@hotmail.com" />
        <Product
          :title="$t('checkout.ticket')"
          :token-amount="100"
          :fiat-amount="100"
        />
      </div>
      <div class="bottom">
        <WalletSwitcher />
        <Button @click="proceed">
          {{ $t('checkout.continue') }}
        </Button>
      </div>
    </template>

    <template v-if="step === 3">
      <div class="top">
        <div class="complete">
          <img
            src="../assets/icons/arrow.svg"
            @click="back"
          >
          <span>{{ $t('checkout.complete') }}</span>
        </div>
        <Success email="denis_davidyuk@hotmail.com" />
        <Product
          :title="$t('checkout.ticket')"
          :token-amount="100"
          :fiat-amount="100"
        />
      </div>
      <div class="bottom">
        <Title
          :top="$t('checkout.login')"
          :bottom="$t('checkout.require-access')"
        />
        <Button
          transparent
          @click="proceed"
        >
          {{ $t('checkout.cancel') }}
        </Button>
      </div>
    </template>

    <template v-if="step === 4">
      <div class="top">
        <div class="complete">
          <img
            src="../assets/icons/arrow.svg"
            @click="back"
          >
          <span>{{ $t('checkout.complete') }}</span>
        </div>
        <Success
          email="denis_davidyuk@hotmail.com"
          address="0xc66ea802717bfb9833400264dd12c2bceaa3"
        />
        <Product
          :title="$t('checkout.ticket')"
          :token-amount="100"
          :fiat-amount="100"
        />
      </div>
      <div class="bottom">
        <Error insufficient />
        <Title
          :top="$t('checkout.wyre-heading')"
          :bottom="$t('checkout.wyre-info')"
        />
        <Button @click="proceed">
          {{ $t('checkout.continue-to-wyre') }}
        </Button>
      </div>
    </template>

    <template v-if="step === 5">
      <div class="top">
        <div class="complete">
          <img
            src="../assets/icons/arrow.svg"
            @click="back"
          >
          <span>{{ $t('checkout.complete') }}</span>
        </div>
        <Success
          email="denis_davidyuk@hotmail.com"
          address="0xc66ea802717bfb9833400264dd12c2bceaa3"
        />
        <Product
          :title="$t('checkout.ticket')"
          :token-amount="100"
          :fiat-amount="100"
        />
      </div>
      <div class="bottom">
        <Title
          :top="$t('checkout.here-we-go')"
          :bottom="$t('checkout.here-we-go-info')"
        />
        <Button @click="proceed">
          {{ $t('checkout.buy-ticket') }}
        </Button>
      </div>
    </template>

    <template v-if="step === 6">
      <div class="top finish">
        <img src="../assets/icons/ok.svg">
        <Title
          :top="$t('checkout.finish-heading')"
          :bottom="$t('checkout.finish-info')"
        />
      </div>
      <div class="bottom">
        <Button>
          {{ $t('checkout.finish-button') }}
        </Button>
      </div>
    </template>
  </div>
</template>

<script>
import Success from '@/components/Success';
import Product from '@/components/Product';
import CreateAccount from '@/components/CreateAccount';
import WalletSwitcher from '@/components/WalletSwitcher';
import Error from '@/components/Error';
import Title from '@/components/Title';
import Button from '@/components/Button';

export default {
  components: {
    Success, Product, CreateAccount, WalletSwitcher, Error, Title, Button,
  },
  data: () => ({
    step: 1,
  }),
  methods: {
    submit(email) {
      console.log(email);
    },
    back() {
      this.step -= 1;
    },
    proceed() {
      switch (this.step) {
        case 1:
          this.step = 2;
          break;
        case 2:
          this.step = this.$store.state.providerName === 'meta-mask' ? 3 : 4;
          break;
        case 3:
          this.step = 2;
          break;
        case 4:
          this.step = 5;
          break;
        case 5:
          this.step = 6;
          break;
        default:
          this.step = 1;
      }
    },
  },
};
</script>

